import React from 'react-dom';
import Card from './Card';

import e from './Images/e.jpg';
import f from './Images/f.jpg';

function Tvshows(){
    return(
        <>
        <div className="container">
        <h4 className="mt-4 text-danger">Tv Shows ... </h4>
            <div className="row">
                <div className="col-md-3">
                <Card src={e} sname="Dark" title="A netflix Orignal Series" />
                </div>
               <div className="col-md-3">
                <Card src={f} sname="Archer" title="Series" />
                </div>
                </div>
                </div>
        </>
    );
}


export default Tvshows;