import React from 'react';
import Card from './Card';
import h from './Images/h.jpg';

function Movies(){
    return(
        <>
        <div class="container">
        <h4 className="mt-4 text-danger"> Movies ... </h4>
        <div class="row">
         <div className="col-md-3">
               <Card src={h} sname="Brighton" title="Orignal series" />
            </div>
            </div>
            </div>

        </>
    );
}
function News(){
    return(
        <>
        <h3 className="text-center text-white mt-3"> No information yet! </h3> 
        </>
    );
}

function Mylist(){
    return(
        <>
        <div className="container">
        <h4 className="mt-4 text-danger"> Continue Watching ... </h4>
            <div className="row">
            
           <div className="col-md-3">
                <Card src="https://d1csarkz8obe9u.cloudfront.net/posterpreviews/adventure-movie-poster-template-design-7b13ea2ab6f64c1ec9e1bb473f345547_screen.jpg?ts=1576732289" sname="Archer" title="Series" />
                </div>
                <div className="col-md-3">
               <Card src="https://occ-0-1001-999.1.nflxso.net/art/a8164/c8094a25c3878e3d28f6d6edfc3858b2063a8164.jpg" sname="IO Last" title="Orignals" />
               </div>
              <div className="col-md-3">
               <Card src={h} sname="Brighton" title="orignal" />
               </div>
               <div className="col-md-3">
               <Card src="https://occ-0-1001-999.1.nflxso.net/art/a8164/c8094a25c3878e3d28f6d6edfc3858b2063a8164.jpg" sname="IO Last" title="Orignals" />
               </div>
               <div className="col-md-3">
               <Card src="https://occ-0-1001-999.1.nflxso.net/art/a8164/c8094a25c3878e3d28f6d6edfc3858b2063a8164.jpg" sname="IO Last" title="Orignals" />
               </div>

                   
            </div>
        </div>
        </>
    );
}
export default Movies;
export {News , Mylist};