import React from 'react';
import './index.css';

function Card(props){
    return(
        <>
        <div className="card">
        <img src={props.src} className="card-img-top" alt="zs" />
        <div className="card-body">
        <p className="card-title">{props.sname}</p>
        <p className="card-text">{props.title} </p>
        <a href="1" class="btn btn-md btn-success">Watch now </a>
        </div>
    

        </div>
        
        </>
    );
}
export default Card;